.h1{
    color: #fff;
    margin-top: 60px;
    margin-left: 80px;
    font-size: 60px;
    position: center;
}

.botonEntrar{
    height: 40px;
    width: 100px;
    margin: 110px;
    margin-top: 250px;
    border-radius: 10px;
    background-color: #FFE33D;
    color: rgb(0, 0, 0);
    font-weight: 600;
    cursor: pointer;
    padding: 10px;
    font-size: 1rem;
    transition: box-shadow 0.2s ease-in-out;
}

.but:hover{
  
    background-color: 0 0 40px #b29e2a;
}

body{
    width: 100vw;
    height: 100vh;
    background-image: url('./Images/001.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.paginaInicio{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 500px;
}